:root {}

body {
  margin: 0;
}

.App {
  display: flex;
  align-items: center;
  justify-content: center;
}

.container {
  margin-top: 2.5rem;
  width: fit-content;
}

.Enter-input {
  display: grid;
  gap: 1rem;
}

.Enter-input button {
  cursor: pointer;
  padding: 1rem 0;
  background: rgb(25, 169, 25);
  color: white;
  font-size: 1rem;
  font-weight: 800;
  border: none;
  border-radius: 5px;
}

.Enter-input button:hover {
  background-color: rgb(20, 140, 20);
}

.result {
  margin-top: 1rem;
}

.result .card {
  padding: 1rem;
  border: 2px solid black;
}